import React, { useState, useEffect, useContext } from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Axios from "axios";
import Swal from "sweetalert2";
import { useHistory } from "react-router";
import { api } from "../../api";
import { boxDataLog } from "./TableData";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    "aria-controls": `vertical-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: "#080f29cc",
    display: "flex",
    height: "60vh",
    padding: "0.5rem",
    margin: "1rem",
    color: "rgb(255, 255, 255)",
  },
  tabs: {
    borderRight: `1px solid ${theme.palette.divider}`,
  },
}));

export default function Sub({ statusLock }) {
  const history = useHistory();
  var firhtTel = "";
  const statusLocks = statusLock;
  // const statusLocks = true;
  const { fechDatalog } = useContext(boxDataLog);
  const classes = useStyles();
  const [value, setValue] = useState(0);
  const [deVice, setDeVice] = useState([]);
  // const [dataLog, setDataLog] = useState([]);
  const [dataLogDevice, setDataLogDevice] = useState([]);
  // const [allTotal, setAllTotal] = useState(0);
  // const [firhtTel, setFirhtTel] = useState([]);
  const getDevice = async () => {
    fetch(api + "device2")
      .then((response) => response.json())
      .then((data) => {
        setDeVice(data.data);
        firhtTel = data.data[0].device2_amount;
        // //console.log(data.data[0].device_tel);
      });
  };

  useEffect(async () => {
    //console.log("111111");
    getDevice();

    setTimeout(() => {
      getLogData(firhtTel);
    }, 300);
  }, []);

  const getLogData = async (data) => {
    fechDatalog();
    fetch(api + "logdevice2/" + data)
      .then((response) => response.json())
      .then((data) => {
        console.log(data);
        setDataLogDevice({
          log11: data.data.online2.count_online2,
          log12: data.data.online2.last_online2,
          log13: data.data.online2.last_online2,
          log21: data.data.coin2.count_coin2,
          log22: data.data.coin2.last_coin2,
          log23: data.data.coin2.sum_coin2,
          log31: data.data.banknote2.count_banknote2,
          log32: data.data.banknote2.last_banknote2,
          log33: data.data.banknote2.sum_banknote2,
          // totals: data.data.allTotals,
        });
      })
      .then(() => {
        // setAllTotal(dataLogDevice.log13+dataLogDevice.log23+dataLogDevice.log33)
      });
  };
  console.log(
    " 1 = " +
      dataLogDevice.log13 +
      " 2 = " +
      dataLogDevice.log23 +
      " 3 = " +
      dataLogDevice.log33
  );
  const allTotal =
    Number(dataLogDevice.log13) +
    Number(dataLogDevice.log23) +
    Number(dataLogDevice.log33);
  // setAllTotal(dataLogDevice.log13+dataLogDevice.log23+dataLogDevice.log33)
  console.log("allTotal = ");
  console.log(allTotal);
  const clearDeviceName = async (tel, name) => {
    await Axios.delete(api + name + "/" + tel);
  };
  const clearDeviceAll = async (bath) => {
    await Axios.delete(api + "clearalldevice2/" + bath);
  };
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const deleteDeName = (tel, name) => {
    Swal.fire({
      title: "ลบ Device",
      text: "ยืนที่จะลบข้อมูล Device ของ " + name,
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "ยืนยัน",
      cancelButtonText: "ยกเลิก",
    }).then((result) => {
      if (result.isConfirmed) {
        // fechDeleteDevice(id, tel);
        clearDeviceName(tel, name);
        history.push("wait");

        Swal.fire({
          position: "center",
          icon: "success",
          title: "ลบข้อมูลสำเร็จ",
          showConfirmButton: false,
          timer: 1500,
        }).then(() => {
          history.push("table-data");
        });
      }
    });
  };

  const deleteDeAll = (tel, name) => {
    Swal.fire({
      title: "ลบ Device",
      text: "ยืนที่จะลบข้อมูล Device ของ " + name,
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "ยืนยัน",
      cancelButtonText: "ยกเลิก",
    }).then((result) => {
      if (result.isConfirmed) {
        // fechDeleteDevice(id, tel);
        clearDeviceAll(tel);
        history.push("wait");
        Swal.fire({
          position: "center",
          icon: "success",
          title: "ลบข้อมูลสำเร็จ",
          showConfirmButton: false,
          timer: 1500,
        }).then(() => {
          history.push("table-data");
        });
      }
    });
  };

  const lock = () => {
    Swal.fire({
      position: "center",
      icon: "info",
      title: "กรุณาปลดล็อค",
      showConfirmButton: false,
      timer: 2000,
    });
  };

  const claerDeviceOnline = (tel, name) => {
    if (statusLocks == 0) {
      lock();
    }
    if (statusLocks == 1) {
      deleteDeName(tel, name);
    }
  };
  const claerDeviceCoin = (tel, name) => {
    if (statusLocks == 0) {
      lock();
    }
    if (statusLocks == 1) {
      deleteDeName(tel, name);
    }
  };
  const claerDeviceBanknote = (tel, name) => {
    if (statusLocks == 0) {
      lock();
    }
    if (statusLocks == 1) {
      deleteDeName(tel, name);
    }
  };

  const claerDevice = (tel) => {
    if (statusLocks == 0) {
      lock();
    }
    if (statusLocks == 1) {
      deleteDeAll(tel);
    }
  };
  return (
    <div className="table-logx">
      <div className="table-log">
        <div className={classes.root}>
          <Tabs
            orientation="vertical"
            variant="scrollable"
            value={value}
            onChange={handleChange}
            aria-label="Vertical tabs example"
            className={classes.tabs}
          >
            {deVice.map((index, key) => {
              return (
                <Tab
                  key={key}
                  label={"device : " + index.device2_amount}
                  {...a11yProps(index.device2_amount)}
                  onClick={() => {
                    getLogData(index.device2_amount);
                  }}
                ></Tab>
              );
            })}
          </Tabs>
          {deVice.map((index, key) => {
            return (
              <TabPanel value={value} index={key}>
                <div className="text-center">
                  <h5 className="title-device">
                    DEVICE : {index.device2_amount}
                  </h5>
                </div>
                <div className="d-block table-device">
                  <div className="d-flex">
                    <span className="title-table">ชื่อ </span>
                    <span className="title-table">จำนวนครั้ง</span>
                    <span className="title-table">ครั้งล่าสุด</span>
                    <span className="title-table">รวม</span>
                    <span className="title-table">Clear</span>
                  </div>
                  <div className="d-flex">
                    <span className="sub-table">ONLINE </span>
                    <span className="sub-table">{dataLogDevice.log11}</span>
                    <span className="sub-table">
                      {Number(dataLogDevice.log12).toFixed(2)}
                    </span>
                    <span className="sub-table">
                      {Number(dataLogDevice.log13).toFixed(2)}
                    </span>
                    <span className="sub-table">
                      <button
                        type="button"
                        class="btn btn-outline-danger"
                        onClick={() => {
                          claerDeviceOnline(index.device2_amount, "clearonline2");
                        }}
                      >
                        Clear
                      </button>
                    </span>
                  </div>
                  <div className="d-flex">
                    <span className="sub-table">เหรียญ </span>
                    <span className="sub-table">{dataLogDevice.log21}</span>
                    <span className="sub-table">
                      {Number(dataLogDevice.log22).toFixed(2)}
                    </span>
                    <span className="sub-table">
                      {Number(dataLogDevice.log23).toFixed(2)}
                    </span>
                    <span className="sub-table">
                      <button
                        type="button"
                        class="btn btn-outline-danger"
                        onClick={() => {
                          claerDeviceCoin(index.device2_amount, "clearcoin2");
                        }}
                      >
                        Clear
                      </button>
                    </span>
                  </div>
                  <div className="d-flex">
                    <span className="sub-table">ธนบัตร </span>
                    <span className="sub-table">{dataLogDevice.log31}</span>
                    <span className="sub-table">
                      {Number(dataLogDevice.log32).toFixed(2)}
                    </span>
                    <span className="sub-table">
                      {Number(dataLogDevice.log33).toFixed(2)}
                    </span>
                    <span className="sub-table">
                      <button
                        type="button"
                        class="btn btn-outline-danger"
                        onClick={() => {
                          claerDeviceBanknote(
                            index.device2_amount,
                            "clearbanknote2"
                          );
                        }}
                      >
                        Clear
                      </button>
                    </span>
                  </div>
                  <div className="d-flex">
                    <span className="subs-table">ยอดรวมทั้งหมด</span>
                    <span className="sub-table">
                      {Number(allTotal).toFixed(2)} บาท
                    </span>
                    <span className="sub-table">
                      <button
                        type="button"
                        class="btn btn-outline-danger"
                        onClick={() => {
                          claerDevice(index.device2_amount);
                        }}
                      >
                        Clear
                      </button>
                    </span>
                  </div>
                </div>
              </TabPanel>
            );
          })}
        </div>
      </div>
    </div>
  );
}
