import React, { useContext, useState } from "react";
import "../../styles.css";
import Swal from "sweetalert2";
import { toggleSW } from "../dashboard/DashbordUnlock";
import { api } from "../../api";
import Axios from "axios";
export default function Toggle({
  statusDevices,
  deviceIDs,
  checkstate,
  deviceBath,
}) {
  const sw = statusDevices;
  const check = checkstate;
  const bath = deviceBath;
  var statusDevice = " ";
  // console.log("check = " + check);

  if (sw == 1) {
    statusDevice = "on";
  }
  if (sw == 0) {
    statusDevice = "off";
  }

  const deviceID = deviceIDs;

  const [toggleState, setToggleState] = useState(statusDevice);
  const { swtogle, setSwTogle } = useContext(toggleSW);

  const updateSw = async ( status, bath) => {
    await Axios.put(api + "device2", {
      device2_state: "0",
      device2_onoff: status,
      device2_amount: bath,
    });
  };

//   {
//     "device2_onoff" : 0,
//     "device2_state" : 0,
//     "device2_amount" :"10.02"
// }

  function toggle() {
    if (check == true) {
      setToggleState(toggleState === "off" ? "on" : "off");
      setSwTogle(toggleState === "off" ? true : false);
      setTimeout(() => {
        updateSw(toggleState === "off" ? 1 : 0,  bath);
      }, 200);
    }
    if (check == false) {
      Swal.fire({
        position: "center",
        icon: "info",
        title: "กรุณาปลดล็อค",
        showConfirmButton: false,
        timer: 2000,
      });
    }
  }
  // console.log(deviceID + " " + swtogle);
  return <div className={`switch ${toggleState}`} onClick={toggle} />;
}
