import React, { useState, useEffect, createContext } from "react";
import Navbar from "../navbar/Navbar";
import Toggle from "../togle/Toggle";
import logoDevice from "../../images/19154.jpg";
import logoDelete from "../../images/delete-icon29.png";
import { api } from "../../api";
import Axios from "axios";
import Swal from "sweetalert2";

import { useHistory } from "react-router";
// import InputTel, { setTextTel } from "./InputTel";

const inputPhone = createContext();
const toggleSW = createContext();
function DashbordUnlock({ stateL, wifiName, wifiPass, lineToken }) {
  const checkLock = stateL;

  // const wifiNameN = wifiName;
  // const wifiPassN = wifiPass;
  // const lineTokenN = lineToken;

  const history = useHistory();
  // const password = "12345";
  const [dataDevice, setDataDevice] = useState([]);
  const [updatePhone, setUpdatePhone] = useState();
  const [swtogle, setSwTogle] = useState();

  // var tel = [];

  useEffect(async () => {
    await Axios.get(api + "device2").then((res) => {
      setDataDevice(res.data.data);
    });
  }, []);

  const lock = () => {
    Swal.fire({
      position: "center",
      icon: "info",
      title: "กรุณาปลดล็อค",
      showConfirmButton: false,
      timer: 2000,
    });
  };

  const insertDevice = async (input) => {
    await Axios.post(api + "device2", {
      device2_amount: input,
    }).then((res) => {
      const error = res.data.err;
      if (error != false) {
        Swal.fire({
          position: "center",
          icon: "error",
          title: "ราคาซ้ำ",
          text: "ไม่สามารถเพิ่มอุปกรณ์ได้",
          showConfirmButton: false,
          timer: 1500,
        }).then(() => {
          history.push("/");
        });
      } else {
        Swal.fire({
          position: "center",
          icon: "success",
          title: "บันทึกสำเร็จ",
          showConfirmButton: false,
          timer: 1500,
        }).then(() => {
          history.push("/");
        });
      }
    });
  };
  // clearall/0620243887

  // const editDevice = async (id, tel, status, bath) => {
  //   if (checkLock == false) {
  //     lock();
  //   } else {
  //     Swal.fire({
  //       title: "แก้ไข DEvice",
  //       text: "ยืนที่จะแก้ไข ข้อมูลทั้งหมดของ Device นี้",
  //       icon: "warning",
  //       showCancelButton: true,
  //       confirmButtonColor: "#3085d6",
  //       cancelButtonColor: "#d33",
  //       confirmButtonText: "ยืนยัน",
  //       cancelButtonText: "ยกเลิก",
  //     }).then(async (result) => {
  //       if (result.isConfirmed) {
  //         history.push("wait");
  //         await Axios.put(api + "device", {
  //           device_id: id,
  //           device_state: "0",
  //           device_onoff: status,
  //           device_tel: updatePhone,
  //           device_bath: bath,
  //         });
  //         Swal.fire({
  //           position: "center",
  //           icon: "success",
  //           title: "แก้ไขข้อมูลสำเร็จ",
  //           showConfirmButton: false,
  //           timer: 1500,
  //         }).then(() => {
  //           history.push("/");
  //         });
  //       }
  //     });
  //   }
  // };

  // console.log();
  const fechDeleteDevice = async (bath) => {
    console.log(api + "device2");
    await Axios.delete(api + "device2/" + bath);
    await Axios.delete(api + "clearalldevice2/" + bath);
  };

  // if (checkLock == false) {
  //   lock();
  // } else {
  // }

  const deleteDevice = (bath) => {
    if (checkLock == false) {
      lock();
    } else {
      // fechDeleteDevice(id, tel);
      //console.log(id);
      //console.log(tel);
      Swal.fire({
        title: "ลบ Device",
        text: "ยืนยันที่จะลบ ข้อมูลทั้งหมดของ Device นี้",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "ยืนยัน",
        cancelButtonText: "ยกเลิก",
      }).then((result) => {
        if (result.isConfirmed) {
          fechDeleteDevice(bath);
          history.push("wait");
          Swal.fire({
            position: "center",
            icon: "success",
            title: "ลบข้อมูลสำเร็จ",
            showConfirmButton: false,
            timer: 1500,
          }).then(() => {
            history.push("/");
          });
        }
      });
    }
  };

  const save = async () => {
    if (checkLock == false) {
      lock();
    } else {
      Swal.fire({
        title: "Add Device",
        text: "เพิ่มราคาของ Device",
        input: "text",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        cancelButtonText: "ยกเลิก",
        confirmButtonText: "บันทึก",
      }).then((result) => {
        if (result.value) {
          const inputPrice = result.value;
          console.log(inputPrice.length);
          insertDevice(inputPrice);
          history.push("wait");
          // Swal.fire({
          //   position: "center",
          //   icon: "success",
          //   title: "บันทึกสำเร็จ",
          //   showConfirmButton: false,
          //   timer: 1500,
          // }).then(() => {
          //   history.push("/");
          // });

          // if (inputPrice.length != 10) {
          //   Swal.fire({
          //     position: "center",
          //     icon: "error",
          //     title: "ผิดพลาด",
          //     text: "กรุณากรอกเบอร์มือถือให้ถูกต้อง ( 10 หลัก )",
          //     showConfirmButton: false,
          //     timer: 3000,
          //   });
          // } else {
          //   insertDevice(inputPrice);
          //   history.push("wait");
          //   Swal.fire({
          //     position: "center",
          //     icon: "success",
          //     title: "บันทึกสำเร็จ",
          //     showConfirmButton: false,
          //     timer: 1500,
          //   }).then(() => {
          //     history.push("/");
          //   });
          // }
        }
      });
    }
  };
  // const [textPhone, setTextPhone] = useState("");
  // console.log("swtogle = " + swtogle);
  // dataDevice.length
  return (
    <div>
      <div className="box-dashbord">
        <div className="row ">
          <toggleSW.Provider value={{ swtogle, setSwTogle }}>
            {dataDevice.map((index, key) => {
              return (
                <div className="col-md-3 " key={key}>
                  <div className="crad-item">
                    <img
                      className="logo-delete"
                      src={logoDelete}
                      onClick={() => {
                        deleteDevice(index.device2_amount);
                      }}
                    />
                    <img className="logo-device" src={logoDevice} />
                    <div>
                      <h4>อุปกรณ์ที่ : {key + 1}</h4>
                      {/* <inputPhone.Provider value={{ textPhone, setTextPhone }}>
                      <InputTel id={index.device_id} tel={index.device_tel} />
                    </inputPhone.Provider> */}

                      <div className="d-flex box-price">
                        <h5>ราคา : </h5>

                        <input
                          className="input-price form-control"
                          type="text"
                          defaultValue={index.device2_amount}
                          onChange={(e) => setUpdatePhone(e.target.value)}
                          maxlength={10}
                          size="10"
                          disabled
                        />
                      </div>

                      <Toggle
                        checkstate={checkLock}
                        deviceIDs={index.device2_id}
                        deviceBath={index.device2_amount}
                        statusDevices={index.device2_onoff}
                      />
                    </div>
                  </div>
                </div>
              );
            })}
          </toggleSW.Provider>

          <div className="col-md-3 ">
            <div className="crad-item">
              <i
                className="bi bi-plus-square-fill icons"
                onClick={() => save()}
              />
              <p className="add-device">Add Device</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
export { toggleSW };
export { inputPhone };
export default DashbordUnlock;
